/**
 * MotorMoreContainer.js
 * More field to fill in for motor
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';
import MotorMore from '../../components/Motor/MotorMore';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { clearMotorData } from '../../actions/motorActions';
import { ampTag } from '../../lib/tagFunctions';

class MotorMoreContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.MORE.index);
    const { clearData } = this.props;
    clearData();
    window.scrollTo(0, 0);

    ampTag('MotorAboutS');
  }

  render() {
    const { motorFields } = this.props;
    if (!motorFields) {
      return null;
    }
    return <MotorMore motorFields={motorFields} />;
  }
}

MotorMoreContainer.defaultProps = {
  motorFields: null,
};

MotorMoreContainer.propTypes = {
  motorFields: PropTypes.object,
  clearData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  motorFields: state.motor.motorFields,
});

const mapDispatchToProps = dispatch => ({
  clearData: () => {
    dispatch(clearMotorData());
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(MotorMoreContainer);
