/**
 * MotorMore.js
 * More details form for Motor
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';
import { reduxForm, getFormValues, change } from 'redux-form';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import DropDownV2 from '../Forms/DropDown/DropDownV2';
import Button from '../FormsV3/Button/Button';
import Selection from '../Forms/Selection/Selection';
import Spinner from '../Forms/Spinner/Spinner';
import ChangeInitialFormData from '../../components/Templates/ChangeInitialFormData/ChangeInitialFormData';

import MotorEditDetail from './MotorEditDetail';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';

import { ampTag } from '../../lib/tagFunctions';
import { validateForm } from '../../lib/validators';

import './MotorMore.scss';

class MotorMore extends Component {
  constructor(props) {
    super(props);

    const { motorFields } = this.props;

    this.state = {
      errorStringData: {},
      yearBornRange: _.range(motorFields.year_born.min, motorFields.year_born.max + 1, 1),
      claimsRange: _.range(0, motorFields.num_claims_made.max + 1, 1),
      errorArray: [],
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  validateForm(e) {
    const { yearBornRange, claimsRange } = this.state;
    const { motorFields } = this.props;

    // console.log(e);
    const errorResult = validateForm(e, [
      'year_born',
      'marital_status',
      'gender',
      'years_driving',
      'number_of_claims',
      'ncd',
      'demerit_status',
    ], {
      year_born: { type: 'ARRAY', data: yearBornRange },
      years_driving: { type: 'ARRAY', data: motorFields.years_driven.options },
      number_of_claims: { type: 'ARRAY', data: claimsRange },
      ncd: { type: 'ARRAY', data: _.range(0, motorFields.ncd.max + 1, 10) },
    });

    this.setState({
      errorStringData: errorResult,
    });

    let errorArray = [];
    if (!_.isEmpty(errorResult)) {
      errorArray = [<p className="error-string" key="error-1">Fields are not properly filled, please check again.</p>];
    }

    this.setState({
      errorArray,
    });

    return errorResult;
  }

  handleSubmit(e) {
    const { changeDetailForm, setInsurFormState, history } = this.props;

    // console.log(e);
    const errorResult = this.validateForm(e);

    if (_.isEmpty(errorResult)) {
      changeDetailForm('marital_status', e.marital_status);
      changeDetailForm('gender', e.gender);
      changeDetailForm('years_of_experience', e.years_driving);
      changeDetailForm('num_claims', e.number_of_claims);
      changeDetailForm('ncd', e.ncd);
      changeDetailForm('offence_free', e.demerit_status);

      ampTag('MotorNextA', {
        born: e.year_born,
        marital: e.marital_status,
        gender: e.gender,
        'driving-years': e.years_driving,
        claims: e.number_of_claims,
        ncd: `${e.ncd || 0}%`,
        demerit: e.demerit_status === 'not demerit points free',
      });

      setInsurFormState(FORMSTATES.CHOOSECOMPANY.index);
      history.push(`/motor${FORMSTATES.CHOOSECOMPANY.path}`);
    }
    window.scrollTo(0, document.body.scrollHeight);
  }

  render() {
    const {
      errorStringData,
      yearBornRange,
      claimsRange,
      errorArray,
    } = this.state;

    const {
      handleSubmit,
      form,
      motorFields,
      storeForm,
    } = this.props;

    return (
      <Fragment>
        <Row className="motor-more" noGutters>
          <Col xs={12} className="landing-form-outer-width">
            <ChangeInitialFormData
              submitCallback={this.submitCallback}
              form="motor"
              valuesToListen={[
                { value: 'vehicle_make', desc: 'Brand' },
                { value: 'vehicle_model', desc: 'Model' },
                { value: 'year_registered', desc: 'Registered' },
              ]}
            >
              <MotorEditDetail isPopUp motorFields={motorFields} />
            </ChangeInitialFormData>
          </Col>
          <Col xs={12}>
            <div className="landing-form-outer-container" style={{ paddingTop: 0 }}>
              <div className="landing-form-container">
                <form onSubmit={handleSubmit(this.handleSubmit)}>
                  <Row className="input-offset" noGutters>
                    <Col className="form-right-padding" xs={12} sm={6}>
                      <DropDownV2
                        form={form}
                        change={this.props.change}
                        name="year_born"
                        placeholderString="Year Born"
                        dropList={yearBornRange}
                        size="medium"
                        zIndex={99}
                        numItemsDisplay={7}
                        desc={motorFields.year_born.display_name}
                        errorString={errorStringData.year_born}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col className="d-none d-sm-flex" sm={6} />
                    <Col className="form-right-padding" xs={12} sm={6}>
                      <Selection
                        form={form}
                        change={this.props.change}
                        name="marital_status"
                        type="text-selection"
                        noEmptyTextSelection
                        options={[
                          { key: 'Single', description: 'Single' },
                          { key: 'Married', description: 'Married' },
                        ]}
                        sizeInSM={6}
                        desc="Marital Status"
                        errorString={errorStringData.marital_status}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col className="form-left-padding" xs={12} sm={6}>
                      <Selection
                        form={form}
                        change={this.props.change}
                        name="gender"
                        type="text-selection"
                        noEmptyTextSelection
                        options={[
                          { key: 'Male', description: 'Male' },
                          { key: 'Female', description: 'Female' },
                        ]}
                        sizeInSM={6}
                        desc="Gender"
                        errorString={errorStringData.gender}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col xs={12}>
                      <hr className="hr-margin" />
                    </Col>
                    <Col xs={12} className="header">
                      <p>Driving experience</p>
                    </Col>
                    <Col className="form-right-padding" xs={12} sm={6}>
                      <DropDownV2
                        form={form}
                        change={this.props.change}
                        name="years_driving"
                        placeholderString="Years Driving"
                        dropList={motorFields.years_driven.options}
                        size="medium"
                        zIndex={98}
                        numItemsDisplay={7}
                        desc={motorFields.years_driven.display_name}
                        errorString={errorStringData.years_driving}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col className="form-left-padding" xs={12} sm={6}>
                      <DropDownV2
                        form={form}
                        change={this.props.change}
                        name="number_of_claims"
                        placeholderString="No of Claims"
                        dropList={claimsRange}
                        size="medium"
                        zIndex={97}
                        numItemsDisplay={7}
                        desc={motorFields.num_claims_made.display_name}
                        errorString={errorStringData.number_of_claims}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col className="form-right-padding" xs={12} sm={6}>
                      <Spinner
                        form={form}
                        change={this.props.change}
                        disableTyping
                        name="ncd"
                        unit="%"
                        min={0}
                        max={motorFields.ncd.max}
                        increment={10}
                        initial={0}
                        desc="I have a no-claim discount of"
                        errorString={errorStringData.ncd}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    <Col className="d-none d-sm-flex" sm={6} />
                    <Col className="demerit-selection" xs={12}>
                      <Selection
                        form={form}
                        change={this.props.change}
                        name="demerit_status"
                        type="text-selection"
                        noEmptyTextSelection
                        options={[
                          { key: 'demerit points free', description: 'demerit points free' },
                          { key: 'not demerit points free', description: 'not demerit points free' },
                        ]}
                        sizeInSM={6}
                        desc={motorFields.demerit_status.display_name}
                        errorString={errorStringData.demerit_status}
                        validateForm={() => this.validateForm(storeForm)}
                      />
                    </Col>
                    {errorArray}
                  </Row>
                  <Row className="align-items-center button-container" noGutters>
                    <Col className="justify-content-center button" xs={12}>
                      <Button type="submit" variant="medium-flat-green" width={165} height={72}>
                        <p>Next</p>
                      </Button>
                    </Col>
                  </Row>
                </form>
              </div>
            </div>
          </Col>
        </Row>
      </Fragment>
    );
  }
}

MotorMore.defaultProps = {
  storeForm: null,
};

MotorMore.propTypes = {
  changeDetailForm: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  motorFields: PropTypes.object.isRequired,
  setInsurFormState: PropTypes.func.isRequired,
  storeForm: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues('motor-more')(state),
});

const mapDispatchToProps = dispatch => ({
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  changeDetailForm: (key, value) => {
    dispatch(change('motor-detail', key, value));
  },
});

export default withRouter(reduxForm({
  form: 'motor-more',
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(MotorMore)));
