/**
 * SpeechScroll.js
 * The hovering dropdown that appears in the navbar and
 * the multi compare view insurer's row.
 *
 * It will auto close when out of the box.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './SpeechScroll.scss';

class SpeechScroll extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isPopUpOpen: false,
      insurerArrowHorizontal: 0,
      scrollPos: 0,
      scrollheight: 0,
      popUpHeight: 0,
      exitCallback: null,
    };

    this.handleInsurerHover = this.handleInsurerHover.bind(this);
    this.handleInsurerBlur = this.handleInsurerBlur.bind(this);
    this.handleMouseMoveOnHoverButton = this.handleMouseMoveOnHoverButton.bind(this);
    this.handleClickOnHoverButton = this.handleClickOnHoverButton.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.handlePopUpScroll = this.handlePopUpScroll.bind(this);
    this.handlePopUpWheel = this.handlePopUpWheel.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    this.hide();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    this.hide();
  }

  show() {
    document.body.scroll = 'no';
    // const el = document.getElementsByTagName('body')[0];
    // el.classList.add('noscroll');
  }

  hide() {
    document.body.scroll = 'yes';
    // const el = document.getElementsByTagName('body')[0];
    // el.classList.remove('noscroll');
  }

  handleScroll() {
    const { isPopUpOpen } = this.state;

    if (isPopUpOpen) {
      this.setState({
        isPopUpOpen: false,
      });

      this.hide();
    }
  }

  handleInsurerHover(event, initCallback = null, exitCallback = null) {
    const { isPopUpOpen } = this.state;
    const { maxHeight } = this.props;

    // console.log('FOCUS');
    if (!isPopUpOpen) {
      let height = 20;
      _.each(this.insurerPopUpScroll.children, (child) => {
        height += child.scrollHeight;
      });
      const rect = event.target.getBoundingClientRect();
      const maxLeft = window.innerWidth - 262 - 20 - 20;
      const defaultLeft = rect.left + rect.width * 0.5 - 131 - 20;
      const left = Math.max(20, Math.min(defaultLeft, maxLeft));

      const arrowDiff = left - defaultLeft;

      this.insurerPopUp.style.left = `${left}px`;
      this.insurerPopUp.style.top = `${rect.top}px`;

      this.setState({
        isPopUpOpen: true,
        insurerArrowHorizontal: arrowDiff,
        popUpHeight: Math.min(height, maxHeight),
        exitCallback,
      });

      this.show();

      if (initCallback) {
        initCallback();
      }

      if (this.insurerPopUpScroll.children.length > 4) {
        setTimeout(this.handlePopUpScroll, 10);
      }
    }
  }

  handleInsurerBlur(e) {
    const { exitCallback } = this.state;

    if (e) {
      e.preventDefault();
    }

    // console.log('BLUR');
    this.setState({
      isPopUpOpen: false,
    });

    if (exitCallback) {
      exitCallback();
    }

    this.hide();
  }

  handleMouseMoveOnHoverButton(event) {
    const rect = this.insurerPopUp.getBoundingClientRect();
    if (event.clientY - rect.top < 40) {
      const { insurerArrowHorizontal } = this.state;
      const leftBound = rect.left + rect.width * 0.5 - 20 - insurerArrowHorizontal;
      const rightBound = leftBound + 60;

      if (!(leftBound < event.clientX && event.clientX < rightBound)) {
        // console.log(rect);
        // console.log(event.clientX);
        this.handleInsurerBlur(event);
      }
    }
  }

  handleClickOnHoverButton(event) {
    const { onHoverButtonClick } = this.props;

    if (!onHoverButtonClick) {
      return;
    }

    const rect = this.insurerPopUp.getBoundingClientRect();
    if (event.clientY - rect.top < 40) {
      const { insurerArrowHorizontal } = this.state;
      const leftBound = rect.left + rect.width * 0.5 - 20 - insurerArrowHorizontal;
      const rightBound = leftBound + 60;

      if (leftBound < event.clientX && event.clientX < rightBound) {
        onHoverButtonClick();
      }
    }
  }

  handlePopUpScroll() {
    const percentage = this.insurerPopUpScroll.scrollTop / (this.insurerPopUpScroll.scrollHeight - this.insurerPopUpScroll.offsetHeight);
    const barheight = this.insurerPopUpScroll.offsetHeight / this.insurerPopUpScroll.scrollHeight * this.insurerPopUpScroll.offsetHeight;
    const offsetY = percentage * (this.insurerPopUpScroll.offsetHeight - barheight);
    // console.log(percentage, this.insurerPopUpScroll.scrollTop, this.insurerPopUpScroll.offsetHeight);
    this.setState({
      scrollPos: offsetY,
      scrollheight: barheight,
    });
    this.show();
  }

  handlePopUpWheel(event) {
    const scrollBottom = this.insurerPopUpScroll.offsetHeight + this.insurerPopUpScroll.scrollTop;
    if (
      (event.deltaY < 0 && this.insurerPopUpScroll.scrollTop <= 0) ||
      (event.deltaY > 0 && scrollBottom === this.insurerPopUpScroll.scrollHeight)
    ) {
      event.preventDefault();
    }
  }

  render() {
    const {
      isPopUpOpen,
      insurerArrowHorizontal,
      scrollPos,
      scrollheight,
      popUpHeight,
    } = this.state;

    const { offsetHeight, onHoverButtonClick } = this.props;

    const openStyle = isPopUpOpen ? '' : 'list-popup-container-closed';
    const hoverButtonStyle = onHoverButtonClick ? 'speech-scroll-clickable' : '';

    return (
      <div className="speech-scroll">
        <div
          role="button"
          tabIndex={-1}
          onKeyUp={() => {}}
          className={`list-popup-container ${hoverButtonStyle} ${openStyle}`}
          ref={node => this.insurerPopUp = node}
          onMouseMove={this.handleMouseMoveOnHoverButton}
          onMouseLeave={this.handleInsurerBlur}
          onClick={this.handleClickOnHoverButton}
          style={{ height: popUpHeight + offsetHeight, paddingTop: offsetHeight }}
        >
          <div
            className="list-popup-background"
            style={{ height: popUpHeight }}
          >
            <div
              className="arrow"
              style={{ marginRight: insurerArrowHorizontal }}
            />
          </div>
          <div className="scrollbar-container" style={{ top: offsetHeight + 2 }}>
            <div
              className="scrollbar"
              ref={node => this.scrollBar = node}
              style={{ marginTop: `${scrollPos}px`, height: scrollheight }}
            />
          </div>
          <div
            className="list-popup"
            onScroll={this.handlePopUpScroll}
            onWheel={this.handlePopUpWheel}
            ref={node => this.insurerPopUpScroll = node}
            style={{ height: popUpHeight }}
          >
            {this.props.children}
          </div>
        </div>
      </div>
    );
  }
}

SpeechScroll.defaultProps = {
  maxHeight: 273,
  offsetHeight: 40,
  onHoverButtonClick: null,
};

SpeechScroll.propTypes = {
  maxHeight: PropTypes.number,
  offsetHeight: PropTypes.number,
  onHoverButtonClick: PropTypes.func,
};

export default SpeechScroll;
