/**
 * Spinner.js
 * Custom Spinner
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Field, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import Plus from 'assets/images/forms/plus.svg';
import Minus from 'assets/images/forms/minus.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './Spinner.scss';

/**
 * Custom Button class
 */
class Spinner extends Component {
  constructor(props) {
    super(props);

    const { initial } = this.props;

    this.state = {
      oldValue: initial,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleOnInputKeyDown = this.handleOnInputKeyDown.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  componentDidMount() {
    const {
      name,
      formStore,
      change,
      initial,
    } = this.props;

    if (!(formStore && formStore[name])) {
      change(name, initial);
      return;
    }

    this.setState({
      oldValue: formStore[name],
    });
  }

  handleChange(event, isMinus) {
    const { oldValue } = this.state;
    const {
      max,
      min,
      name,
      increment,
      change,
    } = this.props;

    const newVal = isMinus ? Math.max(oldValue - increment, min) :
      Math.min(oldValue + increment, max);

    this.setState({
      oldValue: newVal,
    });

    change(name, newVal);
    event.preventDefault();
  }

  handleOnInputKeyDown(event) {
    if (event.keyCode === 13) {
      this.handleInputChange();
      event.preventDefault();
    }
  }

  handleInputChange() {
    const { oldValue } = this.state;

    const {
      min,
      max,
      name,
      change,
    } = this.props;

    const newVal = Math.floor(this.inputField.value);
    if (!Number.isNaN(newVal) && min <= newVal && newVal <= max) {
      change(name, newVal);
    } else {
      change(name, oldValue);
    }
  }

  render() {
    const {
      name,
      unit,
      desc,
      extraInfo,
      errorString,
      disableTyping,
    } = this.props;

    const errorStyle = errorString ? 'error' : '';

    return (
      <FormInputWrapper
        desc={desc}
        ref={node => (this.formParent = node)}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div
          className={`spinner-container ${errorStyle}`}
          tabIndex={0}
          role="button"
        >
          <button
            onClick={event => this.handleChange(event, true)}
            tabIndex={-1}
          >
            <img src={Minus} alt="minus.svg" />
          </button>
          <div
            className="inner-container"
            onBlur={this.handleInputChange}
          >
            <Field
              disabled={disableTyping}
              name={name}
              component="input"
              size="3"
              tabIndex={-1}
              onKeyDown={event => this.handleOnInputKeyDown(event)}
              ref={node => (this.inputField = node)}
            />
            <div className="unit-container">
              <p>{unit}</p>
            </div>
          </div>
          <button
            onClick={event => this.handleChange(event, false)}
            tabIndex={-1}
          >
            <img src={Plus} alt="plus.svg" />
          </button>
        </div>
      </FormInputWrapper>
    );
  }
}

Spinner.defaultProps = {
  unit: '',
  desc: null,
  formStore: null,
  extraInfo: null,
  errorString: null,
  disableTyping: false,
};


Spinner.propTypes = {
  initial: PropTypes.number.isRequired,
  max: PropTypes.number.isRequired,
  min: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  increment: PropTypes.number.isRequired,
  change: PropTypes.func.isRequired,
  unit: PropTypes.string,
  desc: PropTypes.string,
  formStore: PropTypes.object,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  disableTyping: PropTypes.bool,
};


const mapStateToProps = (state, ownProps) => ({
  formStore: (getFormValues(ownProps.form)(state)),
});

export default connect(mapStateToProps, null)(Spinner);
