/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getFormValues, change } from 'redux-form';
import PropTypes from 'prop-types';

import EditButton from 'assets/images/forms/EditButton.svg';

import SpeechScroll from '../SpeechScroll/SpeechScroll';
import Popup from '../Popup/Popup';

import { resetSubmitStatus } from '../../../actions/editFormActions';
import { simplifyCost } from '../../../lib/helperFunctions';

import './ChangeInitialFormData.scss';

class ChangeInitialFormData extends Component {
  constructor(props) {
    super(props);

    this.state = {
      toolTipData: [],
    };

    this.renderHeader = this.renderHeader.bind(this);
    this.renderDetailLabels = this.renderDetailLabels.bind(this);
    this.renderDetailValues = this.renderDetailValues.bind(this);
    this.renderEditButton = this.renderEditButton.bind(this);
    this.renderSpeechScrollList = this.renderSpeechScrollList.bind(this);
  }

  componentDidMount() {
    const { resetSubmit } = this.props;

    resetSubmit();
  }

  shouldComponentUpdate(nextProps, nextState) {
    const { closePopUp, toolTipData, speechScroll } = this.state;
    const { resetSubmit } = this.props;

    if (nextProps.isEditSubmitted) {
      resetSubmit();
    }

    return nextProps.isEditSubmitted ||
          closePopUp !== nextState.closePopUp ||
          toolTipData !== nextState.toolTipData ||
          speechScroll !== nextState.speechScroll;
  }

  renderHeader() {
    const { headerValue, storeForm } = this.props;
    if (storeForm && headerValue) {
      const value = storeForm[headerValue] ? storeForm[headerValue] : '';
      return <span className="edit-detail-header">{value}</span>;
    }

    return null;
  }

  renderDetailLabels() {
    const { valuesToListen, storeForm, conditions } = this.props;
    const conditonValue = conditions ? storeForm[conditions.key] : null;
    const valuesArrayToListen = conditonValue ? conditions.values[conditonValue] : null;

    return (
      <div className="edit-detail-label-container">
        {_.reduce(valuesToListen, (result, data, index) => {
          if (
            !valuesArrayToListen ||
            (valuesArrayToListen && _.findIndex(valuesArrayToListen, value => value === data.value) !== -1)
          ) {
            result.push(<span key={`label-${index}`} className="detail-label">{data.desc}</span>);
          }
          return result;
        }, [])}
      </div>
    );
  }

  renderDetailValues() {
    const { speechScroll } = this.state;
    const { storeForm, valuesToListen, conditions } = this.props;

    const conditonValue = conditions ? storeForm[conditions.key] : null;
    const valuesArrayToListen = conditonValue ? conditions.values[conditonValue] : null;

    if (!storeForm || !speechScroll) {
      return null;
    }

    return (
      <div className="edit-detail-value-container">
        {
          _.reduce(valuesToListen, (result, data, index) => {
            if (
              !valuesArrayToListen ||
              (valuesArrayToListen && _.findIndex(valuesArrayToListen, value => value === data.value) !== -1)
            ) {
              const value = storeForm[data.value] ? storeForm[data.value] : 'None';
              const isArray = value.constructor === Array;

              let displayValue = isArray ? [...value] : value;
              if (data.aggregateDesc && isArray && displayValue.length > 1) {
                displayValue = `${displayValue.length} ${data.aggregateDesc}`;
              }

              if (data.extra === 'cost_simplify') {
                displayValue = simplifyCost(value);
              }

              const jsx = (
                <div key={`detail-${index}`}>
                  <span className="detail-value">
                    {displayValue}
                    {
                      isArray && displayValue.length > 1 ?
                        <div
                          className="tooltip-button"
                          onMouseEnter={() => this.setState({
                            toolTipData: value,
                          })}
                          onMouseMove={(event) => {
                            speechScroll.handleInsurerHover(event);
                          }}
                        >
                          <p>i</p>
                        </div>
                      :
                        null
                    }
                  </span>
                </div>
              );

              result.push(jsx);
            }

            return result;
          }, [])
        }
      </div>
    );
  }

  renderSpeechScrollList() {
    const { toolTipData } = this.state;
    return _.map(toolTipData, (data, index) => <p className="tooltip-text" key={`tooptip-${index}`}>{data}</p>);
  }

  renderEditButton() {
    /* if (!storeForm) {
      return null;
    } */

    return (
      <div
        className="edit-container"
        role="button"
        tabIndex={-1}
        onKeyDown={() => {}}
        onClick={() => {
          this.popupRef.openPopup();
        }}
      >
        <img src={EditButton} alt="EditButton.svg" />
      </div>
    );
  }

  render() {
    const { submitCallback } = this.props;

    return (
      <div className="change-form-container">
        <div className="edit-detail-container">
          {this.renderHeader()}
          <div className="edit-detail-inner-container">
            {this.renderDetailLabels()}
            {this.renderDetailValues()}
          </div>
          {this.renderEditButton()}
        </div>
        <SpeechScroll
          ref={node => !this.state.speechScroll && this.setState({ speechScroll: node })}
        >
          {this.renderSpeechScrollList()}
        </SpeechScroll>
        <Popup ref={(r) => { this.popupRef = r; }}>
          {React.cloneElement(this.props.children, {
            submitCallback,
          })}
        </Popup>
      </div>
    );
  }
}

ChangeInitialFormData.defaultProps = {
  submitCallback: null,
  conditions: null,
  headerValue: null,
};

ChangeInitialFormData.propTypes = {
  valuesToListen: PropTypes.array.isRequired,
  storeForm: PropTypes.object.isRequired,
  isEditSubmitted: PropTypes.bool.isRequired,
  resetSubmit: PropTypes.func.isRequired,
  submitCallback: PropTypes.func,
  conditions: PropTypes.object,
  headerValue: PropTypes.string,
};

const mapStateToProps = (state, ownProps) => ({
  storeForm: getFormValues(ownProps.form)(state),
  isEditSubmitted: state.editForm.isEditSubmitted,
});

const mapDispatchToProps = dispatch => ({
  undoFormChange: (form, key, value) => {
    dispatch(change(form, key, value));
  },
  resetSubmit: () => {
    dispatch(resetSubmitStatus());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ChangeInitialFormData);
